import React from "react";

import Button from 'react-bootstrap/Button';
import Col from 'react-bootstrap/Col';
import Form from 'react-bootstrap/Form';
import Row from 'react-bootstrap/Row';
import Container from 'react-bootstrap/Container';

function NewJob() {

  let emailRef = React.useRef('');
  let titleRef = React.useRef('');
  let locRef = React.useRef('');
  let compRef = React.useRef('');
  let jobTypeRef = React.useRef('');
  let workplaceRef = React.useRef('');
  let descRef = React.useRef('');
  let nameRef = React.useRef('');
  let ageRef = React.useRef('');

  let [result, setResult] = React.useState("");


  const handleSubmitJob = (event) => {

    event.preventDefault();

    const newJob = {
      email: emailRef.current.value,
      title: titleRef.current.value,
      jobLocation: locRef.current.value,
      company: compRef.current.value,
      jobType: jobTypeRef.current.value,
      workplaceRef: workplaceRef.current.value,
      description: descRef.current.value,
      age: ageRef.current.value,
      name: nameRef.current.value,
    };

    try {
      import("../../model/db").then(async (db) => {
        setResult(await db.create([newJob],"jobs"))
      });
    } catch (error) {
      emailRef.current.value = newJob.email;
      titleRef.current.value = newJob.title;
      locRef.current.value = newJob.jobLocation;
      compRef.current.value = newJob.company;
      jobTypeRef.current.value = newJob.jobType;
      workplaceRef.current.value = newJob.workplaceRef;
      descRef.current.value = newJob.description;
      ageRef.current.value = newJob.age;
      nameRef.current.value = newJob.name;
      console.warn(error)
    }

    if (result==="success"){
      emailRef.current.value = "";
      titleRef.current.value = "";
      locRef.current.value = "";
      compRef.current.value = "";
      jobTypeRef.current.value = "";
      workplaceRef.current.value = "";
      descRef.current.value = "";
      ageRef.current.value = "";
      nameRef.current.value = "";
    }

    event.stopPropagation();
  }


  return (
    <Container className="w-75">
      <Form onSubmit={(ev) => handleSubmitJob(ev)}>
        <Row className="mb-3">

          <Form.Group as={Col} controlId="email">
            <Form.Label>Email</Form.Label>
            <Form.Control type="email" placeholder="Enter email" ref={emailRef} />
          </Form.Group>

          <Form.Group as={Col} controlId="title">
            <Form.Label>Job Tilte</Form.Label>
            <Form.Control type='text' placeholder="Software Developer" ref={titleRef} />
          </Form.Group>
        </Row>

        <Row>
          <Form.Group as={Col} controlId="title">
            <Form.Label>Full Name</Form.Label>
            <Form.Control type='text' placeholder="Jone Doe" ref={nameRef} />
          </Form.Group>

          <Form.Group as={Col} className="mb-3" controlId="age">
            <Form.Label>Age</Form.Label>
            <Form.Control placeholder="14" ref={ageRef} />
          </Form.Group>

        </Row>

        <Row>

          <Form.Group as={Col} controlId="jobType">
            <Form.Label>Job Type</Form.Label>
            <Form.Select defaultValue="Choose..." ref={jobTypeRef}>
              <option>Choose...</option>
              <option>Full-time</option>
              <option>Part-time</option>
              <option>Contract</option>
              <option>Volunteer</option>
            </Form.Select>
          </Form.Group>

          <Form.Group as={Col} controlId="workplaceType">
            <Form.Label>Work Place Type</Form.Label>
            <Form.Select defaultValue="Choose..." ref={workplaceRef} >
              <option>Choose...</option>
              <option>Remote</option>
              <option>On-site</option>
              <option>Hybrid</option>
            </Form.Select>
          </Form.Group>

        </Row>

        <Row>
          <Form.Group as={Col} className="mb-3" controlId="company">
            <Form.Label>Company</Form.Label>
            <Form.Control placeholder="Google Inc" ref={compRef} />
          </Form.Group>

          <Form.Group as={Col} className="mb-3" controlId="location">
            <Form.Label>Job Location</Form.Label>
            <Form.Control placeholder="Kogi State" ref={locRef} />
          </Form.Group>
        </Row>

        <Form.Group className="mb-3" controlId="description">
          <Form.Label>Description</Form.Label>
          <Form.Control as="textarea" placeholder="Describe ..." ref={descRef} />
        </Form.Group>

        <div className="text-center">
          <span className="d-block mb-2">{result.result}</span>
          <Button variant="primary" type="submit">Submit</Button>
        </div>


      </Form>
    </Container>
  );
}

export default NewJob;