
import { users, jobs } from "./data";

let dbpromise = new Promise((resolve, reject) => {

    //  get indexedDB instance
    let idb = window.indexedDB;
    // set the database name
    let dbName = "recruiterdb";
    //   oepen database with version
    const request = idb.open(dbName, 5);
    // on error, log it
    request.onerror = (event) => {
        console.log("DB error: " + event.target.errorCode);
        reject(event.target.errorCode)
    }

    // on success, log it
    request.onsuccess = (event) => {
        let db = event.target.result;
        userTransaction(db);
        jobTransaction(db);
        resolve(db)
        // close db when version change
        db.onversionchange = (event) => {
            db.close();
        }
    }

    request.onupgradeneeded = (event) => {

        const db = event.target.result;

        // Create an userObjectStore to hold information about our users. We're
        // going to use "ssn" as our key path because it's guaranteed to be
        // unique - or at least that's what I was told during the kickoff meeting.
        const userObjectStore = db.createObjectStore("users", { keyPath: "email" });

        const jobObjectStore = db.createObjectStore("jobs", { keyPath: "email" });

        // Create an index to search users by email. We want to ensure that
        // no two users have the same email, so use a unique index.
        userObjectStore.createIndex("email", "email", { unique: true });

        jobObjectStore.createIndex("email", "email", { unique: true })

    };


})

function userTransaction(db) {

    const transaction = db.transaction(["users"], "readwrite");

    const userObjectStore = transaction.objectStore("users");
    users.forEach((user) => {
        const request = userObjectStore.add(user);
        request.onsuccess = (event) => {
            // event.target.result === customer.ssn;
        };
    });

    transaction.onerror = (event) => {
        // Don't forget to handle errors!
        if (event.target.errorCode) console.log(event.target.errorCode)
    };

    transaction.oncomplete = (event) => {
        console.log("All done!")
    }
}

function jobTransaction(db) {

    const transaction = db.transaction(["jobs"], "readwrite");
    const jobObjectStore = transaction.objectStore("jobs");
    jobs.forEach((job) => {
        const request = jobObjectStore.add(job);
        request.onsuccess = (event) => {
            console.log(event.target.result === job.email)
        };
    });

    transaction.onerror = (event) => {
        // Don't forget to handle errors!
        if (event.target.errorCode) console.log(event.target.errorCode);
    };

    transaction.oncomplete = (event) => {
        console.log("All done!")
    }
}


function create(data = [], store = "") {

    let newPromise = new Promise((resolve, reject) => {

        dbpromise.then((db) => {
            const transaction = db.transaction([store], "readwrite");
            // Do something when all the data is added to the database.

            const userObjectStore = transaction.objectStore(store);

            data.forEach((customer) => {
                const request = userObjectStore.add(customer); // add data

                request.onsuccess = (event) => {
                    // resolve(event.target.result)
                    resolve({ result: "success" })
                };

                request.onerror = (event) => {
                    // Handle errors!
                    reject({ result: event.target.errorCode });
                };

            });
        })
    });

    return newPromise;
}


function readById(id = "", store = "") {
    let newPromise = new Promise((resolve, reject) => {

        dbpromise.then((db) => {

            const transaction = db.transaction([store]);
            const userObjectStore = transaction.objectStore(store);
            const request = userObjectStore.get(id);

            request.onerror = (event) => {
                // Handle errors!
                reject(event.target.errorCode)
            };

            request.onsuccess = (event) => {
                // Do something with the request.result!
                resolve(request.result)
            };

        })

    })

    return newPromise;

}

function readAll() {
    let newPromise = new Promise((resolve, reject) => {

        dbpromise.then((db) => {
            const transaction = db.transaction(["jobs"]);
            const userObjectStore = transaction.objectStore("jobs");

            const request = userObjectStore.getAll();

            request.onerror = (event) => {
                // Handle errors!
                reject(event.target.errorCode);
                console.log(event.target.errorCode)
            };

            request.onsuccess = (event) => {
                // Do something with the request.result!
                resolve(request.result)
                console.log(request.result);
            };

        })

    })

    return newPromise;

}

function readAllByCursor() {
    // alternatively
    let customersPromise = new Promise((resolve, reject) => {
        dbpromise.then((db) => {
            const users = [];
            db.transaction(["users"]).objectStore('users').openCursor().onsuccess = (event) => {
                const cursor = event.target.result;
                if (cursor) {
                    users.push(cursor.value);
                    cursor.continue();
                } else {
                    // console.log(users);
                    resolve(users);
                }
            };

        })
    });

    return customersPromise;
}


function remove(id = "", store = "") {
    let delPromise = new Promise((resolve, reject) => {
        dbpromise.then((db) => {
            const request = db
                .transaction([store], "readwrite")
                .objectStore(store)
                .delete(id);

            request.onsuccess = (event) => {
                // It's gone!
                resolve({ result: "success" })
            };

            request.onerror = (event) => {
                // Handle errors!
                reject({ result: event.target.errorCode });
            };

        })
    })
    return delPromise;
}

function update(item = {}, store = "") {
    let updPromise = new Promise((resolve, reject) => {

        dbpromise.then((db) => {

            const transaction = db.transaction([store], "readwrite");

            const objStore = transaction.objectStore(store);
            // Put this updated object back into the database.
            const requestUpdate = objStore.put(item);

            requestUpdate.onerror = (event) => {
                // Do something with the error
                reject({ result: event.target.errorCode });
            };
            requestUpdate.onsuccess = (event) => {
                // Success - the data is updated!
                resolve({ result: "success" })
            };
        });
    })

    return updPromise;
}

export { readAll, readById, create, update, remove, readAllByCursor };