import React from "react";
import Button from "react-bootstrap/Button";
import Alert from 'react-bootstrap/Alert';
import { useNavigate, useParams } from "react-router-dom";

function DeleteJob() {
    const { id } = useParams();
    const [result, setResult]=React.useState({});
    const navigate = useNavigate();

    const handleDelete =()=>{
        try {
            import("../../model/db").then(async(db)=>{
                const res= await db.remove(id,'jobs');
                setResult(res);
                setTimeout(() => {
                    navigate("/recruiter/jobs");
                }, 3000);
           }); 
        } catch (error) {
            console.log(error)
        }
    }

    return (
        <Alert variant="success">
            <Alert.Heading>Hey, Want to Delete?</Alert.Heading>
            <p>
               {result.result??"Are you sure you want to do this?"} 
            </p>
            <hr />
            <p className="mb-0">
               <Button variant="success" onClick={()=>navigate("/recruiter/jobs/"+id+"/edit")}>Cancel</Button>&nbsp;&nbsp;
               <Button variant="danger" onClick={()=>handleDelete()} >Delete </Button>
            </p>
        </Alert>
    )
}

export default DeleteJob;