import React, { Suspense, } from 'react';

import './sass/App.scss';
import {
  BrowserRouter,
  Routes,
  Route,
} from "react-router-dom";
import PrivateRoute from './features/auth/PrivateRoute';
import AuthContext from './features/auth/AuthContext';
import Optin from './features/offer/Optin';
import UserDashboard from './features/user/UserDashboard';
// const NewJob = React.lazy(() => import("./features/user/job/NewJob"));
// const EditJob = React.lazy(() => import("./features/user/job/EditJob"));
// const ViewJobs = React.lazy(() => import("./features/user/job/ViewJobs"));
// const ViewJob = React.lazy(() => import("./features/user/job/ViewJob"));

import "./model/db";
import IndexJob from './features/job';
import ViewJobs from './features/job/ViewJobs';
import ViewJob from './features/job/ViewJob';
import NewJob from './features/job/NewJob';
import EditJob from './features/job/EditJob';
import DeleteJob from './features/job/DeleteJob';
const Hire = React.lazy(() => import('./features/offer/Hire'));
const Book = React.lazy(() => import('./features/offer/Book'));

const Offer = React.lazy(() => import('./features/offer/Index'));
const FreeOffer = React.lazy(() => import("./features/offer/PaidOffer"));
const PaidOffer = React.lazy(() => import("./features/offer/FreeOffer"));

const Blog = React.lazy(() => import("./features/blog"));
const SinglePost = React.lazy(() => import("./features/blog/SinglePost"));
const CreatePost = React.lazy(() => import("./features/blog/CreatePost"));
const EditPost = React.lazy(() => import("./features/blog/EditPost"));
const DeletePost = React.lazy(() => import("./features/blog/DeletePost"));
const ReadPosts = React.lazy(() => import("./features/blog/ReadPosts"));

const Admin = React.lazy(() => import("./features/admin"));
const AdminDashboard = React.lazy(() => import("./features/admin/dashboard"));

const Service = React.lazy(() => import("./features/service"));
const Career = React.lazy(() => import("./features/career"));
const About = React.lazy(() => import("./features/about"));
const Home = React.lazy(() => import("./features/home"));
const Layout = React.lazy(() => import("./features/layout"));
const Signin = React.lazy(() => import("./features/auth/Signin"));

const ErrorBoundary = React.lazy(() => import("./components/ErrorBoundary"));
const PageNotFound = React.lazy(() => import("./features/pagenotfound"));
const Spinner = React.lazy(() => import("./components/Spinner"))

const Message = React.lazy(() => import("./features/contact/Message"));
const Location = React.lazy(() => import("./features/contact/Location"))
const Contact = React.lazy(() => import("./features/contact"));

const Auth = React.lazy(() => import("./features/auth"));
const Signup = React.lazy(() => import("./features/auth/Signup"));
const ForgetPassword = React.lazy(() => import("./features/auth/ForgetPassword"))
const ChangePassword = React.lazy(() => import("./features/auth/ChangePassword"))
const PasswordConfirmation = React.lazy(() => import("./features/auth/ConfirmPassword"))

const User = React.lazy(() => import("./features/user"));

function App() {

  return (
    <AuthContext>
      <BrowserRouter>
        <ErrorBoundary>
          <Suspense fallback={<Spinner />}>
            <Routes>

              <Route path={"recruiter"} element={<Layout />}>
                <Route index element={<Home />} />
                <Route path='home' element={<Home />} />              
                <Route path='about' element={<About />} />

                <Route path='contact' element={<Contact />}>
                  <Route index element={<Message />} />
                  <Route path='location' element={<Location />} />
                </Route>

                <Route path='career' element={<Career />} />
                <Route path='service' element={<Service />} />

                <Route path='blogs' element={<Blog />}>
                  <Route index element={<ReadPosts />} />
                  <Route path=':id' element={<SinglePost />} />
                  <Route path='create' element={<CreatePost />} />
                  <Route path='edit' element={<EditPost />} />
                  <Route path='delete' element={<DeletePost />} />
                </Route>

                <Route path='users' element={<User />}>
                  <Route index element={<PrivateRoute><UserDashboard /></PrivateRoute>} />
                </Route>

                <Route path='jobs' element={<IndexJob/>} >
                  <Route index element={<ViewJobs/>} />
                  <Route path=':id' element={<ViewJob/>} />
                  <Route path='new' element={<NewJob/>} />
                  <Route path=':id/edit' element={<EditJob/>} />
                  <Route path=':id/delete' element={<DeleteJob/>} />
                </Route>

                <Route path='offers' element={<Offer />} >
                  <Route path='free' element={<FreeOffer />} />
                  <Route path='paid' element={<PaidOffer />} />
                  <Route path='hire' element={<Hire />} />
                  <Route path='book' element={<Book />} />
                  <Route path='optin' element={<Optin />} />
                </Route>

                <Route path='admin' element={<Admin />}>
                  <Route index element={<PrivateRoute><AdminDashboard /></PrivateRoute>} />
                </Route>

                <Route path='auth' element={<Auth />}>
                  <Route path='signin' element={<Signin />} />
                  <Route path='signup' element={<Signup />} />
                  <Route path='forget' element={<ForgetPassword />} />
                  <Route path='change' element={<ChangePassword />} />
                  <Route path='confirm' element={<PasswordConfirmation />} />
                </Route>

                <Route path='*' element={<PageNotFound />} />
              </Route>
            </Routes>
          </Suspense>
        </ErrorBoundary>
      </BrowserRouter>
    </AuthContext>
  );
}

export default App;
